.login-container {
    align-items: center;
    display: grid;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}

.login-container .row {
    flex-grow: 1;
    flex-shrink: 1;
    background-color: #ffffff;
}

.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
}
.login-form-1 h3{
    text-align: center;
    color: #333;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
    background: #0062cc;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
    outline: none;
    transition: all 300ms;
}

.btnSubmit:hover {
    color: #fff !important;
    background-color: #2c3e50 !important;
}

.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}

.login-form-2 .btnSubmit{

    background-color: #fff;
    color: #0062cc;
}

.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

@media screen and (min-width: 768px) {
    .login-form-1, .login-form-2 {
        min-width: 380px;
    };
}

@media screen and (min-width: 1100px) {
    .login-form-1, .login-form-2 {
        min-width: 500px;
    };
}